<template>
    <template v-if="props.item?.id">
        <template v-if="!props.item.template || props.item.template === 'ROW_BIG'">
            <Widgets
                :loadMore="false"
                type="row"
                :open="true"
                :light="false"
                :params="{
                    sectionId: props.sectionId,
                    parentId: props.item.id,
                    showed: true
                }"
                :title="{
                    text: props.item.name,
                    img: props.item.cover?.resized ? {
                        preview: props.item.cover.resized,
                        original: props.item.cover.original,
                        skeleton: true,
                        aspectRatio: '1 / 1',
                        width: '48px'
                    } : null
                }"
                @open="emit('open', $event)"
                @change="emit('change', $event)"
            />
        </template>

        <template v-else-if="props.item.template === 'ROW'">
            <Widgets
                :loadMore="false"
                type="row"
                :open="true"
                :light="true"
                :params="{
                    sectionId: props.sectionId,
                    parentId: props.item.id,
                    showed: true
                }"
                :title="{
                    text: props.item.name,
                    img: props.item.cover?.resized ? {
                        preview: props.item.cover.resized,
                        original: props.item.cover.original,
                        skeleton: true,
                        aspectRatio: '1 / 1',
                        width: '48px'
                    } : null
                }"
                @open="emit('open', $event)"
                @change="emit('change', $event)"
            />
        </template>

        <template v-else-if="props.item.template === 'LIST'">
            <Widgets
                :loadMore="false"
                type="list"
                :open="true"
                :params="{
                    sectionId: props.sectionId,
                    parentId: props.item.id,
                    showed: true
                }"
                :title="{
                    text: props.item.name,
                    img: props.item.cover?.resized ? {
                        preview: props.item.cover.resized,
                        original: props.item.cover.original,
                        skeleton: true,
                        aspectRatio: '1 / 1',
                        width: '48px'
                    } : null
                }"
                @open="emit('open', $event)"
                @change="emit('change', $event)"
            />
        </template>
    </template>
</template>

<script setup>
    import Widgets from 'ðŸ”—/components/blocks/Widgets.vue';

    const emit = defineEmits(['change', 'open']);

    const props = defineProps({
        sectionId: { type: [String, null], default: null },
        item: { type: [Object, null], default: null }
    });
</script>

