<template>
    <Block code="widgets"
        v-bind="props"
        ref="block"
        @change="onClick"
        @open="emit('open')"
        :sliderProps="{
            itemWidth: props.light ? '96px' : '128px',
            // itemHeight: '208px',
            colAlign: 'start',
            colGap: '8px',
            rowGap: '8px',
            snapType: 'none'
        }"
    >
        <template #list-item="{ item }">
            <WidgetPreview
                :row="props.subtype === null ? true : props.subtype === 'row'"
                :light="props.light"
                :item="item"
                :showKebab="props.showKebab"
            />
        </template>

        <template #list-skeleton>
            <WidgetPreview
                :row="props.subtype === null ? true : props.subtype === 'row'"
                :playable="false"
                :light="props.light"
                :skeleton="true"
            />
        </template>

        <template #row-item="{ item }">
            <WidgetPreview
                :row="props.subtype === 'row' || false"
                :playable="false"
                :light="props.light"
                :item="item"
                :showKebab="props.showKebab"
            />
        </template>

        <template #row-skeleton>
            <WidgetPreview
                :row="props.subtype === 'row' || false"
                :light="props.light"
                :playable="false"
                :skeleton="true"
            />
        </template>
    </Block>
</template>

<script setup>
    import Block from 'ðŸ”—/components/blocks/Block.vue';

    import { ref } from 'vue';
    import WidgetPreview from 'ðŸ”—/components/ui/WidgetPreview.vue';
    import stores from '@/stores';

    const emit = defineEmits(['change', 'open']);

    const nav = stores.nav();
    
    const block = ref(null);

    const props = defineProps({
        empty: { type: Object, default: () => ({}) },
        items: { type: Array, default: () => ([]) },
        title: { type: [Object, String, null], default: null },
        params: { type: Object, default: () => ({}) },
        sort: { type: [Object, null], default: () => ({
            code: 'position',
            direction: 'asc'
        }) },
        light: { type: Boolean, default: true },
        showKebab: { type: Boolean, default: false },
        loadMore: { type: Boolean, default: true },
        skeleton: { type: Boolean, default: true },
        open: { type: Boolean, default: false },
        skeletonCount: { type: Number, default: 3 },
        itemsInCol: { type: Number, default: 1 },
        take: { type: Number, default: 30 },
        handler: { type: [String, null], default: () => null },
        type: { type: String, default: 'list' },
        source: { type: [Object, null], default: () => ({ store: 'showcase', fnName: 'listWidget' }) },
        buttons: { type: Array, default: () => ([]) },
        getById: { type: [String, null], default: null },
        subtype: { type: [String, null], default: null }
    });

    const reload = () => {
        if (block?.value?.reload) {
            block.value.reload();
        }
    };

    const onClick = (item) => {
        emit('change', item);

        // if (props.handler === 'release' && item?.id) {
        //     nav.open('release', { id: item?.id });
        // }
    }

    defineExpose({
        reload
    });

</script>
