        align-items: stretch;
        gap: 8px;
        width: 100%;
        overflow: hidden;
        & > div {
            &:first-child {
                display: flex;
                flex-direction: column;
                gap: 8px;
                position: relative;

                .cover {
                    width: 128px;
                    height: 128px;
                    position: relative;

                    .skeleton-wrapper,
                    img {
