                        height: 100%;
                        border-radius: 10px;
                        object-fit: cover;
                        overflow: hidden;
                    }

                    &.disabled {
                        filter: grayscale(1);
                        .skeleton-wrapper,
                        img {
                            cursor: default;
                        }
                    }
                }

            }

            &:nth-child(2) {
                display: flex;
                flex-direction: column;
                gap: 4px;
                flex: 1;
                min-width: 0;

                & > div {
                    display: flex;
                    flex-direction: row;
                    align-items: baseline;
                    gap: 4px;
                    min-width: 0;

                    &:first-child {
                        flex-wrap: nowrap;
                    }
                    &:last-child {
                        flex-direction: column;
                        width: 100%;
                        span {
                            display: flex;
                            flex-direction: row;
                            gap: 4px;
                        }
                    }

                    .name {
                        font: var(--font-body-1);
                        color: var(--dark-500);
                        // white-space: nowrap;
                        // text-overflow: ellipsis;
                        // overflow: hidden;
                    }
                }
            }
            &:nth-child(3) {
                display: flex;
                flex-direction: column;
