                    }
                }
                &:nth-child(2) {
                    & > div {
                        .name {
                            font: var(--font-body-2);
                            // color: var(--dark-500);
                            // white-space: nowrap;
                            // text-overflow: ellipsis;
                            // overflow: hidden;
                        }
                    }
                }
            }
        }

        &.disabled {
            opacity: .4;
            filter: grayscale(1);
            cursor: default;
        }

        &.row {
            gap: 12px;
            flex-direction: row;
            & > div {
                &:first-child {
                    .cover {
                        width: 80px;
                        height: 80px;
                    }

                }
                &:nth-child(2) {
                    & > div {
                        &:first-child {
                        }
                        &:last-child {
                            align-items: start;
                        }
                    }
