                    }
                }
            }
        }

        &.skeleton {
            & > div:nth-child(2) {
                & > div {
                    position: relative !important;
                    color: transparent !important;
                    @include skeleton;

                    &:nth-child(1) {
                        max-width: 120px;
                        .name {
                            opacity: 0;
                        }
                    }
                    &:nth-child(2) {
                        max-width: 60px;
                        button {
