<template>
    <div class="head" :class="{ 'with-cover': !!data.widget?.headerCover }">
        <Img
            v-if="data.widget?.headerCover"
            :preview="data.widget?.headerCover?.resized"
            :original="data.widget?.headerCover?.original"
            :skeleton="true"
            :checktone="true"
            :alt="data.widget.name"
            aspectRatio="113 / 57"
            @tone="data.tone = $event"
        />

        <Menu :items="data.sections"
            :code="data.section?.code"
            :useI18n="false"
            @change="setSection"
        />
        <div v-if="data.widget?.id"
            class="widget-head">
            <IconButton class="tertiary size-l"
                icon="chevron-left"
                @click="back"
            />

            <div>
                <TitleWithButtons class="size-l"
                    :title="{
                        text: data.widget.name
                    }"
                />

                <div class="description" v-if="data.widget?.description">
                    {{ data.widget.description }}
                </div>
            </div>
        </div>
    </div>

    <template v-for="item in data.widgets" :key="item.id">
        <template v-if="item.type === 'EDITORIAL'">
            <WidgetArtists v-if="item.artists?.length" :item="item" />
            <WidgetTracks v-else-if="item.tracks?.length" :item="item" />
            <WidgetReleases v-else-if="item.releases?.length" :item="item" />
            <WidgetPlaylists v-else-if="item.playlists?.length" :item="item" />
            <SubWidgets v-else
                :item="item"
                :sectionId="data.section?.id"
                @change="openSubWidget($event, item)"
                @open="openWidget(item)"
            />
        </template>
        <template v-if="item.type === 'STATIC'">
            <template v-if="data.widgetComponents[item.code]">
                <component :is="data.widgetComponents[item.code]" :name="{ text: item.name }" />
            </template>
        </template>
    </template>
</template>

<script setup>
    import { reactive, watch, defineAsyncComponent, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';

    import WidgetArtists from './widgets/WidgetArtists.vue';
    import WidgetTracks from './widgets/WidgetTracks.vue';
    import WidgetReleases from './widgets/WidgetReleases.vue';
    import WidgetPlaylists from './widgets/WidgetPlaylists.vue';
    import SubWidgets from './widgets/SubWidgets.vue';
    import TitleWithButtons from 'ðŸ”—/components/ui/TitleWithButtons.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import Menu from 'ðŸ”—/components/ui/Menu.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';

    import stores from '@/stores';
    import api from 'ðŸ”—/api';
    import { markRaw } from 'vue'


    const { t } = useI18n();

    // const modals = stores.modals();
    const showcase = api.showcase();

    const props = defineProps({
        page: { type: String, default: 'MUSIC' }
    });

    const staticMap = {
        'more-discoveries': async () => defineAsyncComponent(() => import('@/pages/Music/components/widgets/static/WidgetMoreDiscoveries.vue'))
    }

    const data = reactive({
        widget: null,
        widgets: [],
        widgetComponents: {},
        parents: [{
            id: props.parentId
        }],
        sections: [],
        section: null
    });

    const loadStaticComponents = async (code) => {
        if (data.widgetComponents[code] || !staticMap[code]) {
            return;
        }

        data.widgetComponents[code] = markRaw(await staticMap[code]());
    }

    const getWidgets = async () => {
        const parent = data.parents[data.parents.length - 1];

        // modals.open('loader', {data: {light: true}})

        // if (parent?.id) {
        //     console.log(await showcase.widget.get(parent.id));
        // }

        data.widgets = [];
        data.widget = parent.widget;

        console.log(data.widget)

        const payload = await showcase.widget.list({
            sectionId: data.section?.id,
            parentId: parent?.id || 'null',
            showed: true
        });

        await Promise.all(payload?.items.filter(item => item.type === 'STATIC').map(async (item) => loadStaticComponents(item.code)));

        const widgets = payload?.items || [];

        if (
            data.widget?.tracks?.length ||
            data.widget?.releases?.length ||
            data.widget?.artists?.length ||
            data.widget?.playlists?.length
        ) {
            widgets.unshift({
                ...data.widget,
                name: null,
                cover: null
            })
        }


        data.widgets = widgets;
        // setTimeout(() => {
        //     modals.close('loader');
        // }, 200);
    }

    const openSubWidget = (payload, parent) => {
        if (!payload?.id || !parent?.id) {
            return;
        }

        data.parents.push({
            id: parent.id,
            widget: parent
        });

        data.parents.push({
            id: payload.id,
            widget: payload
        });

        getWidgets();
    }

    const openWidget = (payload) => {
        if (!payload?.id) {
            return;
        }

        data.parents.push({
            id: payload.id,
            widget: payload
        });

        getWidgets();
    }

    const setSection = (code) => {
        data.section = data.sections.find(item => item.code === code);

        data.parents = [
            {
                id: null
            }
        ];

        getWidgets();
    }

    const getSections = async () => {
        // Ð¡ÐµÐºÑ†Ð¸Ð¸
        let payload = await showcase.section.list({
            showed: true,
            page: props.page
        });

        data.sections = payload?.items || [];
        data.section = data.sections[0];

        getWidgets();
    }

    const back = () => {
        if (data.parents.length > 1) {
            data.parents.pop();
            getWidgets();
        }
    }

    watch(
        () => props.page,
        () => {
            getSections();
        }
    );

    onMounted(() => {
        getSections();
    });
</script>

<style lang="scss" scoped>
    .head {
        @include page-title;
        margin-top: 28px;
        margin-bottom: 32px;
        display: flex;
        flex-direction: column;
        gap: 24px;
        height: auto;

        ul {
            margin: 0 auto;
        }

        &.with-cover {
            aspect-ratio: 113 / 57;
            margin: 0;
            // padding: 0;
            padding: 28px 0 24px 14px;
            position: relative;

            .skeleton-wrapper {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
            }

            .menu {
                z-index: 1;
            }

            .widget-head {
                z-index: 1;
            }

            &:before {
                content: '';
                inset: 0;
                position: absolute;
                background: var(--white-80);
                z-index: 1;
            }
                
            &:after {
                content: '';
                position: absolute;
                bottom: -1px;
                left: 0;
                width: 100%;
                height: 36px;
                background: linear-gradient(to bottom, var(--white-0) 0%, var(--white-500) 100%);
                pointer-events: none;
                z-index: 1;
            }
        }
    }

    .widget-head {
        display: flex;
        flex-direction: row;
        align-items: baseline;
        gap: 12px;
        padding: 0;
        width: 100%;

        & > div {
            display: flex;
            flex-direction: column;
            gap: 8px;

            .description {
                font: var(--font-body-1);
                max-width: 280px;
                color: var(--dark-80);
            }
        }
    }
</style>
